<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>  
        <el-card>
            <el-table :data='rightsList' stripe>
                <el-table-column type='index'></el-table-column>
                <el-table-column label='' prop='authName'></el-table-column>
                <el-table-column label='' prop='path'></el-table-column>
                <el-table-column label='' prop='level'>
                    <template slot-scope='scope'>
                       <el-tag v-if="scope.row.level==='0'"></el-tag>
                       <el-tag v-else-if="scope.row.level==='1'" type='success'></el-tag>
                       <el-tag v-else type='warning'></el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>
<script>
export default {
    name:'rights',
    created(){
        this.getRightsList()
    },
    data() {
        return {
            rightsList:[]
        }
    },
    methods: {
        getRightsList(){
            this.$http.get('rights/list')
            .then(res=>{
                this.rightsList=res.data.data
            })
        }
    },
}
</script>
