<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-alert title="" type="info" center show-icon> </el-alert>
            <el-steps :active="parseInt(activeIndex)" finish-status="success" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <el-form :model="addFrom" :rules="addFromRule" ref="addFromRef" label-width="100px" label-position='top'>
                <el-tabs :tab-position="'left'" v-model='activeIndex' :before-leave='beforeTbaLeave'
                @tab-click='tabClicked'>
                    <el-tab-pane label="" name='0'>
                        <el-form-item label='' prop='goods_name'>
                            <el-input v-model='addFrom.goods_name'></el-input>
                        </el-form-item>
                        <el-form-item label='' prop='goods_price'>
                            <el-input v-model='addFrom.goods_price'></el-input>
                        </el-form-item>
                        <el-form-item label='' prop='goods_weight'>
                            <el-input v-model='addFrom.goods_weight'></el-input>
                        </el-form-item>
                        <el-form-item label='' prop='goods_number'>
                            <el-input v-model='addFrom.goods_number'></el-input>
                        </el-form-item>
                        <el-form-item label='' prop='goods_cat'>
                            <el-cascader v-model="addFrom.goods_cat" :options="cateList" :props="cateProp"
                                @change="handleChange"></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name='1'></el-tab-pane>
                    <el-tab-pane label="" name='2'></el-tab-pane>
                    <el-tab-pane label="" name='3'></el-tab-pane>
                    <el-tab-pane label="" name='4'></el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
    </div>
</template>
<script>
    export default {
        name: 'AddGoods',
        data() {
            return {
                activeIndex: '0',
                addFrom: {
                    goods_name: '',
                    goods_price: 0,
                    goods_weight: 0,
                    goods_number: 0,
                    goods_cat: []
                },
                addFromRule: {
                    goods_name: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    goods_price: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    goods_weight: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    goods_number: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    goods_cat: [
                        { required: true, message: '', trigger: 'blur' },
                    ]
                },
                cateList: [],
                cateProp: {
                    expandTrigger: 'hover',
                    label: 'cat_name',
                    value: 'cat_id',
                    children: "children"
                }
            }
        },
        methods: {
            getCateList() {
                this.$http.get('categories')
                    .then(res => {
                        this.cateList = res.data.data
                    })
            },
            handleChange() {
                if (this.addFrom.goods_cat.length != 3) {
                    this.addFrom.goods_cat = []
                }
                //

                console.log(this.addFrom.goods_cat)
            },
            beforeTbaLeave(activeName, oldActiveName) {
                if (  oldActiveName === '0'&& this.addFrom.goods_cat.length !== 3 ) {
                    alert('')
                    console.log(this.addFrom.goods_cat.length)
                    return false
                }
                // return false
            },

            tabClicked(){
                if(this.activeIndex='1'){
                    this.$http.get('categories/'+this.cateId+'/attributes',{
                        params:{sel:'many'}
                    }).then(res=>{
                        console.log(res)
                        // console.log(this.addFrom.goods_cat)
                    })
                }
                console.log(this.activeIndex)
            }
        },
        created() {
            this.getCateList()
        },
        computed: {
            cateId(){
                if(this.addFrom.goods_cat.length=3){
                    return this.addFrom.goods_cat[2]
                }
                return null
                // return this.addFrom.goods_cat[2]
            }
        },
    }
</script>
<style>
    div .el-step__title {
        font-size: 13px;
    }
</style>